import React, { Component } from 'react'
import { Modal } from 'react-bootstrap';
import { SketchField, Tools } from 'react-sketch';
import SmallHorseFront from '../../assets/images/SmallHorseFront.png';
import SmallHorseLeft from '../../assets/images/SmallHorseLeft.png';
import SmallHorseRight from '../../assets/images/SmallHorseRight.png';
import HorseFront from '../../assets/images/HorseFront.png';
import HorseLeft from '../../assets/images/HorseLeft.png';
import HorseRight from '../../assets/images/HorseRight.png';
export default class DrawMarkModal extends Component {
    constructor(props) {
        super(props);
        this.state = {
            bigHorseImages: {
                left: HorseLeft,
                right: HorseRight,
                front: HorseFront
            },
            selectedHorse: 'left',
            drawings: [],
            canUndo: false,
            canRedo: false,
            drawImage: {
                left: '',
                right: '',
                front: ''
            },
            lineWidth: 3,
            lineColor: 'red',
            sketchWidth: 1000,
            sketchHeight: 600,
            stretched: true,
            stretchedX: false,
            stretchedY: false,
        };

    }

    componentDidUpdate(prevProps) {
        if (this.props.display !== prevProps.display) {
           this._onImageUpdate(this.state.bigHorseImages.left)
           this.setState({
               drawImage: this.props.drawedImage
           })
        }
        
    }

    handleSelectImage = (type) => {
        let drawImage = { ...this.state.drawImage };
        drawImage[type] = ''
        this.setState({
            drawImage,
            selectedHorse: type
        })
        this._onImageUpdate(this.state.bigHorseImages[type])
    }

    _onSketchChange = () => {
        let prev = this.state.canUndo;
        let now = this._sketch.canUndo();
        if (prev !== now) {
            this.setState({ canUndo: now });
        }
    };

    _onImageUpdate= (accepted) => {
        let sketch = this._sketch;
        let opts = {
            left: 50,
            top: 10,
            scale: 0.5,
            sketchWidth: 1000,
            sketchHeight: 500,
            stretched: true,
            stretchedX: false,
            stretchedY: false,
        };
        if (sketch !== undefined) {
            sketch.clear()
            this.setState({
                canUndo: this._sketch.canUndo(),
                canRedo: this._sketch.canRedo(),
            });
            var image = new Image();
            image.onload = function () {
                var canvas = document.createElement('canvas');
                canvas.width = this.naturalWidth; // or 'width' if you want a special/scaled size
                canvas.height = this.naturalHeight; // or 'height' if you want a special/scaled size

                canvas.getContext('2d').drawImage(this, 0, 0);
                sketch.setBackgroundFromDataUrl(canvas.toDataURL('image/png'), opts)
            };

            image.src = accepted;
            
            //sketch.addImg(accepted, opts)
        }
    };
    
    _undo = () => {
        this._sketch.undo();
        this.setState({
            canUndo: this._sketch.canUndo(),
            canRedo: this._sketch.canRedo(),
        });
    };

     _download = () => {
         let drawImage = {...this.state.drawImage};
         drawImage[this.state.selectedHorse] = this._sketch.toDataURL()
         this.setState({
             drawImage
         })
    };

    handleClose = () => {
        this.props.drawImage(this.state.drawImage)
        this.props.onHide();
    }
    
    render() {
        const { display } = this.props;
        console.log(this.props);
        
        return (
            <Modal className="modal modal-common fade" id="draw-mark-modal" show={display}
                onHide={this.handleClose}>
                <button onClick={this.handleClose} type="button" className="close" data-dismiss="modal">&times;</button>
                <div className="modal-dialog modal-dialog-centered modal-xl">
                    <div className="modal-content">
                        <div className="modal-header">
                            <h4 className="modal-title">Draw the Mark Location</h4>
                        </div>
                        <div className="modal-body">

                            <div className="draw-slider-view-root">

                                <div className="row">
                                    <div className="col-lg-3 col-md-4 order-md-1 order-2">

                                        <div className="slider-list-view">
                                            <div className="row-list">
                                                <div className={this.state.selectedHorse === 'left' ? "draw-box-div active" : "draw-box-div"}>
                                                    {this.state.drawImage.left !== '' && <div className="saved-layer" onClick={() => {
                                                        if (this.state.drawImage.left !== '') {
                                                            if (window.confirm('Image Already Created, Still you want to reacreate image?')) { this.handleSelectImage('left') }
                                                        } else {
                                                            this.handleSelectImage('left')
                                                        }
                                                    }
                                                    }></div>} 
                                                    <img 
                                                        onClick={() => { 
                                                            if (this.state.drawImage.left !== '' ) 
                                                            { 
                                                                if (window.confirm('Image Already Created, Still you want to reacreate image?')) { this.handleSelectImage('left') }
                                                            }else{
                                                                this.handleSelectImage('left')
                                                            }
                                                        }
                                                    }
                                                    // onClick={() => this.handleSelectImage('left')} 
                                                        src={this.state.drawImage.left !== '' ? this.state.drawImage.left : SmallHorseLeft} className="img-fluid img-fit" alt="horse" />
                                                    {this.state.drawImage.left !== '' && <label>Saved</label>}
                                                </div>
                                                <div className={this.state.selectedHorse === 'right' ? "draw-box-div active" : "draw-box-div"}>
                                                    {this.state.drawImage.right !== '' && <div className="saved-layer" onClick={() => {
                                                        if (this.state.drawImage.right !== '') {
                                                            if (window.confirm('Image Already Created, Still you want to reacreate image?')) { this.handleSelectImage('right') }
                                                        } else {
                                                            this.handleSelectImage('right')
                                                        }
                                                    }}></div>} 
                                                    <img onClick={() => { 
                                                        if (this.state.drawImage.right !== '' ) 
                                                            { 
                                                            if (window.confirm('Image Already Created, Still you want to reacreate image?')) { this.handleSelectImage('right') }
                                                            }else{
                                                            this.handleSelectImage('right')
                                                            }
                                                    }} src={this.state.drawImage.right !== '' ? this.state.drawImage.right : SmallHorseRight} className="img-fluid img-fit" alt="horse" />
                                                    {this.state.drawImage.right !== '' && <label>Saved</label>}
                                                </div>
                                                <div className={this.state.selectedHorse === 'front' ? "draw-box-div active" : "draw-box-div"}>
                                                    {this.state.drawImage.front !== '' && <div className="saved-layer" onClick={() => {
                                                        if (this.state.drawImage.front !== '') {
                                                            if (window.confirm('Image Already Created, Still you want to reacreate image?')) { this.handleSelectImage('front') }
                                                        } else {
                                                            this.handleSelectImage('front')
                                                        }
                                                    }} ></div>}                                                    <img onClick={() =>{
                                                        if (this.state.drawImage.front !== '' )
                                                    {
                                                            if (window.confirm('Image Already Created, Still you want to reacreate image?')) {this.handleSelectImage('front')}
                                                            }else{
                                                        this.handleSelectImage('front')
                                                    }
                                                    }} src={this.state.drawImage.front !== '' ? this.state.drawImage.front  : SmallHorseFront} className="img-fluid img-fit" alt="horse" />
                                                    {this.state.drawImage.front !== '' && <label>Saved</label>}
                                                </div>
                                            </div>
                                        </div>

                                    </div>
                                    <div className="col-lg-9 col-md-8 order-md-2 order-1">

                                        <div className="active-view-draw">
                                            <div className="img-cover-box">
                                                <div className="d-flex justify-content-end">  <button
                                                    className="btn btn-primary btn-undo"
                                                    color="primary"
                                                    disabled={!this.state.canUndo}
                                                    onClick={this._undo}>
                                                   <span className="material-icons mr-2">undo</span>Undo
                                                </button></div>
                                                <div className="d-flex justify-content-center">
                                                <SketchField
                                                    name="sketch"
                                                    tool={Tools.Pencil}
                                                    className="canvas-area"
                                                    ref={c => (this._sketch = c)}
                                                    lineColor={this.state.lineColor}
                                                    lineWidth={this.state.lineWidth}
                                                    fillColor={
                                                        '#ffffff'
                                                    }
                                                    backgroundColor={
                                                       '#ffffff'
                                                    }
                                                    width={
                                                       this.state.sketchWidth
                                                    }
                                                    height={
                                                       this.state.sketchHeight
                                                    }
                                                    value={null}
                                                    forceValue
                                                    onChange={this._onSketchChange}
                                                    
                                                />
                                                </div>
                                                

                                            </div>
                                        </div>

                                    </div>
                                </div>

                            </div>

                        </div>
                        <div className="modal-footer pt-xs0">
                            <div className="col-footer">
                                <div className="btn-col width-xs-120 pr-10px">
                                    <button onClick={this.handleClose} type="button" className="btn btn-secondary" data-dismiss="modal">Cancel</button>
                                </div>
                                <div className="btn-col">
                                    <button onClick={this._download} className="btn btn-primary btn-save">Save <span className="display-none-sm">&nbsp;the mark</span> </button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </Modal>
        )

    }
}