import React, { Component } from 'react'
import Camera from '../../camera';
import { Modal } from 'react-bootstrap';
export default class CameraPopupModal extends Component {
    constructor() {
        super();
        this.webcam = null;
        this.state = {
            capturedImage: null,
            captured: false,
            uploading: false
        }
    }
    componentDidMount() {
        // initialize the camera
        
    }
    componentDidUpdate(prevProps) {
        if (this.props.display !== prevProps.display) {
            if (this.props.display){
                this.canvasElement = document.createElement('canvas');
                this.webcam = new Camera(
                    document.getElementById('webcam'),
                    this.canvasElement
                );
                this.setupCamera();
            }
            
        }
    }

    componentWillUnmount() {
        // this.webcam = new Camera(
        //     document.getElementById('webcam'),
        //     this.canvasElement
        // );
        // if (this.webcam !== undefined){
        //     this.webcam.stopCamera();
        // }
        
    }

    setupCamera = () => {
        this.webcam.setup().catch(() => {
            alert('Error getting access to your camera');
        });
    }

    captureImage = async () => {
        const capturedData = this.webcam.takeBase64Photo({ type: 'jpeg', quality: 1 });
        this.setState({
            captured: true,
            capturedImage: capturedData.base64
        });
        this.webcam.stopCamera();
    }

    discardImage = () => {
        this.setState({
            captured: false,
            capturedImage: null
        })
        this.setupCamera();
    }
    uploadImage = () => {
        this.props.onCaptureImage(this.state.capturedImage);
        this.discardImage();
        
    }
    handleClose = () => {
        this.webcam.stopCamera();
        this.props.onHide();
        
    }
    render() {
        const { display } = this.props;
        const imageDisplay = this.state.capturedImage ?
            <img src={this.state.capturedImage} alt="captured" width="350" />
            :
            <span />;

        const buttons = this.state.captured ?
            <div>
                <div className="row justify-content-center">
                    <div className="col-lg-6 col-md-8">
                        <div className="btn-group-ulpoad">
                            <button className="deleteButton btn btn-secondary" onClick={this.discardImage} > Delete Photo </button>
                            <button className="captureButton btn btn-primary" onClick={this.uploadImage} > Upload Photo </button>
                        </div>
                    </div>
                </div>
                
            </div> :
            <div className="row justify-content-center">
            <div className="col-lg-3 col-md-4">
                <button className="captureButton btn btn-primary" onClick={this.captureImage} > Take Picture  </button>
            </div>
            </div>
            
            

        const uploading = this.state.uploading ?
            <div><p> Uploading Image, please wait ... </p></div>
            :
            <span />

        return(
            <Modal className="modal modal-common modal-800 fade" id="draw-mark-modal" show={display}
                onHide={this.handleClose}>
                <button onClick={this.handleClose} type="button" className="close" data-dismiss="modal">&times;</button>
                <div className="modal-dialog modal-dialog-centered modal-dialog-800">
                    <div className="modal-content">
                        <div className="modal-header">
                            <h4 className="modal-title">Capture Image of animal</h4>
                        </div>
                        <div className="modal-body camera-popup">

                            <div className="draw-slider-view-root">

                                <div className="row">
                                    <div className="col-lg-12 col-md-12 order-md-2 order-1">
                                        
                                        {uploading}
                                        <video autoPlay playsInline muted id="webcam" width="100%" height="200"
                                            style={{
                                                'display': this.state.capturedImage ? 'none' : 'flex'
                                            }} />
                                        <br />
                                        <div className="imageCanvas" style={{
                                            'display': this.state.capturedImage ? 'flex' : 'none'
                                        }}>
                                            {imageDisplay}
                                        </div>
                                        {buttons}
                                    </div>
                                </div>

                            </div>

                        </div>
                        <div className="modal-footer pt-xs0">
                            <div className="col-footer">
                                <div className="btn-col ">
                                    <button onClick={this.handleClose} type="button" className="btn btn-secondary" data-dismiss="modal">Close</button>
                                </div>
                                {/* <div className="btn-col">
                                    <button onClick={this._download} className="btn btn-primary btn-save">Save <span className="display-none-sm">&nbsp;the mark</span> </button>
                                </div> */}
                            </div>
                        </div>
                    </div>
                </div>
            </Modal>
        )
    }
}