/* eslint-disable array-callback-return */
import React, { Component } from 'react'
import DropDownList from '../ui/DropDownList';
import InputField from '../ui/InputField';
import "flatpickr/dist/themes/material_blue.css";
import Flatpickr from "react-flatpickr";
import { getUrl } from '../../services/network/urls';
import { postFormData,putFormData, get } from '../../services/network/requests';
import _ from 'lodash';
import DrawMarkModal from './DrawMarkModal';
import CameraPopupModal from './CameraPopupModal';
 import { brandPositions,} from "../../staticData/mocks";
import { Link } from 'react-router-dom'
import moment from 'moment';


export default class HorsePermanentTravelPermitForm extends Component {
    constructor(props) {
        super(props);
        this.state = {
            horseId : 0,
            breed: [],
            color:[],
            brand: [],
            sex: [],
            location: brandPositions,
            city: [
                { value: 0, label: 'Beaver', type: 'city' },
                { value: 1, label: 'Hooper', type: 'city' },
                { value: 2, label: 'Provo', type: 'city' },
                { value: 3, label: 'Salt Lake', type: 'city' },
            ],
            
            photoGallery: [
            ],
            renderMarkModal: false,
            selectedBreed: '',
            selectedColor: '',
            selectedBrand: '',
            selectedSex: '',
            selectedLocation: '',
            selectedCity: '',
            approxHeight: '',
            tattoo:'',
            name: '',
            registration: '',
            costOfPermit: '',
            liveStockInspector: '',
            ownerName: '',
            ownerAddress: '',
            ownerZipCode: '',
            phone: '',
            yearOfBorn: '',
            inspectDate: '',
            isCertified: false,
            isPermited: false,
            yearOfBornError: '',
            inspectDateError: '',
            selectedBreedError: '',
            selectedColorError: '',
            selectedBrandError: '',
            selectedSexError: '',
            selectedLocationError: '',
            selectedCityError: '',
            approxHeightError: '',
            nameError: '',
            registrationError: '',
            costOfPermitError: '',
            liveStockInspectorError: '',
            ownerNameError: '',
            ownerAddressError: '',
            ownerZipCodeError: '',
            phoneError: '',
            isCertifiedError: false,
            isPermitedError: false,
            activeSuggestionOwner: false,
            filteredSuggestions: [],
            ownerData: [],
            invalidImage: '',
            drawImage:[],
            cameraModal:false,
            stateOptions: [],
            countyOptions: [],
            countyOptionsOriginal: [],
            isEdit : false,
            ownerId:'',
            
        };

    }
    async componentDidMount() {
        await this.getAutoCompleteData();
        await this.getAnimalInitData();
        if (this.props.match.params.id !== undefined){
            await this.getHorsePermanntTravel();
        }
    }

    getHorsePermanntTravel = () => {
        const url = getUrl("print_horse_permanent_travel_permit");
        get(`${url}/${this.props.match.params.id}/`)
            .then(res => {
                const { data: { code, data, status } } = res;
                switch (code) {
                    case 200:
                        if (status) {
                            console.log(data);
                            let ownerState = this.state.stateOptions.filter((e) => e.label === data.state);
                            let ownerCounty = this.state.countyOptionsOriginal.filter((e) => e.label === data.county);
                            if (ownerState.label !== "UTAH") {
                                this.setState({
                                    countyOptions: this.state.defualtCountyOptions,
                                })
                                ownerCounty = this.state.defualtCountyOptions;
                            }
                            let breed = this.state.breed.filter((e) => e.label === data.breed.breed)
                            let color = this.state.color.filter((e) => e.label === data.color.color)
                            let brand = this.state.brand.filter((e) => e.label === data.brand.brand)
                            let sex = this.state.sex.filter((e) => e.label === data.sex)
                            let drawImage = {
                                left: data.mark_left.image !== undefined ? data.mark_left.image: '',
                                right: data.mark_right.image !== undefined ? data.mark_right.image: '',
                                front: data.mark_center.image !== undefined ? data.mark_center.image: ''
                            }
                            this.setState({
                               horseId :data.id,
                               photoGallery:data.animal_photo,
                               approxHeight: data.approx_ht,
                               tattoo : data.tattoo,
                               name:  data.name,
                               registration: data.registration_no,
                               costOfPermit: data.cost_of_permit,
                               liveStockInspector: data.live_stock_inspector,
                               ownerName: data.owner_name,
                               ownerAddress: data.owner_street_address,
                                ownerZipCode: data.zipcode,
                               ownerCity: data.city,
                               phone: data.phone,
                               yearOfBorn:data.year_of_born,
                               inspectDate: data.date,
                                selectedBreed: breed[0],
                                selectedColor: color[0],
                                selectedBrand: brand[0],
                                selectedSex: sex[0],
                                selectedLocation: '',
                                ownerState: ownerState[0],
                                ownerCounty: ownerCounty[0],
                                drawImage: drawImage,
                                isCertified: data.is_certified,
                                isPermited:  data.is_permited
                           },() => {
                               console.log(this.state);
                               
                           })
                        }
                        break;

                    default:
                        break;
                }
            })
            .catch(error => console.log(error))
    }

    getAutoCompleteData = () => {
        const url = getUrl("stockman_init");
        get(url)
            .then(res => {
                const { data: { code, data, status } } = res;
                switch (code) {
                    case 200:
                        if (status) {
                            const stateOption = data.state.map(({ id: value, state: label }) => ({ value, label }))
                            const countyOption = data.county.map(({ id: value, county: label }) => ({ value, label }))
                            let defualtCountyOptions = countyOption.filter((e) => e.label === 'Out-of-State');
                            this.setState({
                                ownerData: data.owner,
                                stateOptions: stateOption,
                                countyOptions: countyOption,
                                countyOptionsOriginal:countyOption,
                                defualtCountyOptions: defualtCountyOptions
                            })
                        }
                        break;

                    default:
                        break;
                }
            })
            .catch(error => console.log(error))
    }

    getAnimalInitData = () => {
        const url = getUrl("get_animal_init");
        get(url)
            .then(res => {
                const animal = res.data.data.Horse;
                this.setState({
                    breed: Object.values(animal.breed).map(({ id: value, name: label }) => ({ value, label, type: "breed" })),
                    color: Object.values(animal.color).map(({ id: value, name: label }) => ({ value, label, type: "color" })),
                    brand: Object.values(animal.brand).map(({ id: value, name: label }) => ({ value, label, type: "brand" })),
                    sex: Object.values(animal.gender).map(({ id: value, name: label }) => ({ value, label, type: "sex" }))
                })
                
            })
            .catch(error => console.log(error))
    }

    handleChangeBreed = (selected) => {
        this.setState({ selectedBreed: selected }, this.handleChange('selectedBreed', selected))
    }
    handleChangeColor = (selected) => {
        this.setState({ selectedColor: selected }, this.handleChange('selectedColor', selected))
    }
    handleChangeBrand = (selected) => {
        this.setState({ selectedBrand: selected }, this.handleChange('selectedBrand', selected))
    }
    handleChangeSex = (selected) => {
        this.setState({ selectedSex: selected }, this.handleChange('selectedSex', selected))
    }
    handleChangeLocation = (selected) => {
        this.setState({ selectedLocation: selected }, this.handleChange('selectedLocation', selected))
    }
    handleChangeCity = (selected) => {
        this.setState({ selectedCity: selected }, this.handleChange('selectedCity', selected))
    }

    handleOpenCameraPopUp = () => {
        this.setState({ cameraModal: !this.state.cameraModal });
    }

    handleChange = (name, value) => {
        switch (name) {
            case 'selectedBreed':
            case 'selectedColor':
            case 'selectedBrand':
            case 'selectedSex':
            case 'selectedLocation':
                if (value === '' || value === undefined) {
                    this.setState({ [name + 'Error']: _.startCase(name) + " is requied" })
                } else {
                    this.setState({ [name + 'Error']: '' })
                }
                break;
            default:
                break;
        }
    }

    onChangeChecked = (e) => {
        const { checked, name } = e.target;
        this.setState({ [name]: checked }, () => {
            switch (name) {
                case 'isPermited':
                    if (!checked) {
                        this.setState({ [name + 'Error']: "Please check permited checkbox." })

                    } else {
                        this.setState({ [name + 'Error']: '' })

                    }
                    break;
                case 'isCertified':
                    if (!checked) {
                        this.setState({ [name + 'Error']: "Please check certified checkbox." })

                    } else {
                        this.setState({ [name + 'Error']: '' })

                    }
                    break;
                default:
                    break;
            }
        });
    }

    onInputChangeHandler = (e) => {
        const { value, name } = e.target;
        this.setState({ [name]: value }, () => {
            switch (name) {
                case 'approxHeight':
                case 'name':
                case 'registration':
                case 'costOfPermit':
                case 'liveStockInspector':
                case 'ownerZipCode':
                case 'inspectDate':
                case 'phone':
                case 'ownerCity':
                    if (value === '') {
                        this.setState({ [name + 'Error']: _.startCase(name) + " is requied" })

                    } else {
                        this.setState({ [name + 'Error']: '' })

                    }
                    break;
                case 'yearOfBorn':
                    if (value === '') {
                        this.setState({ [name + 'Error']: "Year is requied" })
                    } else if (!Number(value)) {
                        this.setState({ [name + 'Error']: 'Input Only number' })
                    }else if (value.length > 4) {
                        this.setState({ [name + 'Error']: "Maximum four charcter" })
                    } else if (Number(value.substring(0, 2)) !== 20 && Number(value.substring(0, 2)) !== 19) {
                        this.setState({ [name + 'Error']: "staring with 1900  or 2000" })
                    }  else {
                        this.setState({ [name + 'Error']: '' })
                    }

                    break;
                case 'ownerName':
                    if (value === '') {
                        this.setState({ ownerNameError: "Owner name is requied", activeSuggestionOwner: false, })

                    } else {
                        const filteredSuggestions = this.state.ownerData.filter(
                            suggestion => suggestion.name.toLowerCase().indexOf(this.state.ownerName.toLowerCase()) > -1
                        );
                        this.setState({ ownerNameError: '', activeSuggestionOwner: true, filteredSuggestions, activeSuggestionBuyer: false })

                    }
                    break;
                case 'ownerAddress':
                    if (value === '') {
                        this.setState({ ownerAddressError: "Owner Address is requied" })

                    } else {
                        this.setState({ ownerAddressError: '', activeSuggestionOwner: false, activeSuggestionBuyer: false })

                    }
                    break;
                default:
                    break;
            }
        });
    }

    handleDeleteImage = (data) => {
        let photoGallery = [...this.state.photoGallery];
        photoGallery.map((item, index) => {
            if (item.id === data.id) {
                photoGallery.splice(index, 1);
            }
            return item;
        });
        this.setState({ photoGallery });
    }
    handleCaptureImage = (captureImage) => {
        let fileArray = [...this.state.photoGallery];
        fileArray.push({
            id: Math.random(),
            image: captureImage
        })
        this.setState({ photoGallery: fileArray })
    }
    uploadMultipleFiles = (e) => {
        let fileObj = [];
        let fileArray = [...this.state.photoGallery];

        fileObj.push(e.target.files)

        for (let i = 0; i < fileObj[0].length; i++) {
            if (!fileObj[0][i].name.match(/\.(jpg|jpeg|png|gif)$/)) {
                this.setState({ invalidImage: 'Some image can not be upload' }, () => {
                    setTimeout(function () {
                        this.setState({ invalidImage: '' })
                    }.bind(this), 5000);
                });

            } else {
                fileArray.push({
                    id: Math.random(),
                    image: URL.createObjectURL(fileObj[0][i])
                })
            }

        }
        this.setState({ photoGallery: fileArray })
    }

    handleModal = () => {
        this.setState({ renderMarkModal: !this.state.renderMarkModal });
    }
    validateField = (name, value) => {
        switch (name) {
            case 'approxHeight':
            case 'name':
            case 'registration':
            case 'costOfPermit':
            case 'liveStockInspector':
            case 'ownerZipCode':
            case 'inspectDate':
            case 'phone':
                if (value === '') {
                    this.setState({ [name + 'Error']: _.startCase(name) + " is requied" })
                    return false
                } else {
                    this.setState({ [name + 'Error']: '' })
                    return true;
                }
            case 'yearOfBorn':
                if (value === '') {
                    this.setState({ [name + 'Error']: "Year is requied" })
                    return false
                } else if (value.length > 4) {
                    this.setState({ [name + 'Error']: "Maximum four charcter" })
                    return false
                } else if (Number(value.substring(0, 2)) !== 20 && Number(value.substring(0, 2)) !== 19) {
                    this.setState({ [name + 'Error']: "staring with 1900  or 2000" })
                    return false
                } if (!Number(value)){
                    this.setState({ [name + 'Error']: 'Input Only number' })
                    return false
                }else {
                    this.setState({ [name + 'Error']: '' })
                    return true;
                }
            case 'ownerName':
                if (value === '') {
                    this.setState({ ownerNameError: "Owner name is requied", activeSuggestionOwner: false, })
                    return false
                } else {
                    const filteredSuggestions = this.state.ownerData.filter(
                        suggestion => suggestion.name.toLowerCase().indexOf(this.state.ownerName.toLowerCase()) > -1
                    );
                    this.setState({ ownerNameError: '', activeSuggestionOwner: true, filteredSuggestions, activeSuggestionBuyer: false })
                    return true;
                }
                
            case 'ownerAddress':
                if (value === '') {
                    this.setState({ ownerAddressError: "Owner Address is requied" })
                    return false
                } else {
                    this.setState({ ownerAddressError: '', activeSuggestionOwner: false, activeSuggestionBuyer: false })
                    return true;
                }
                
            case 'isPermited':
                if (!value) {
                    this.setState({ [name + 'Error']: "Please check permited checkbox." })
                    return false
                } else {
                    this.setState({ [name + 'Error']: '' })
                    return true;
                }
                
            case 'isCertified':
                if (!value) {
                    this.setState({ [name + 'Error']: "Please check certified checkbox." })
                    return false
                } else {
                    this.setState({ [name + 'Error']: '' })
                    return true;
                }
                
            case 'selectedBreed':
            case 'selectedColor':
            case 'selectedBrand':
            case 'selectedSex':
            case 'ownerCity':
                if (value === '' || value === undefined) {
                    this.setState({ [name + 'Error']: _.startCase(name) + " is requied" })
                    return false
                } else {
                    this.setState({ [name + 'Error']: '' })
                    return true;
                }
                
            default:
                return true;
                
        }
    }

    isFormValid = (data) => {
        let isValid = true
        for (let [key, value] of Object.entries(data)) {
            let validation = this.validateField(key, value)
            if (isValid) {
                isValid = validation;
            }
            
        }
        return isValid
    }

    handleSave = () => {
        const data = {
            selectedBreed: this.state.selectedBreed,
            selectedColor: this.state.selectedColor,
            selectedBrand: this.state.selectedBrand,
            selectedSex: this.state.selectedSex,
            selectedLocation: this.state.selectedLocation,
            ownerCity: this.state.ownerCity,
            approxHeight: this.state.approxHeight,
            name: this.state.name,
            registration: this.state.registration,
            costOfPermit: this.state.costOfPermit,
            liveStockInspector: this.state.liveStockInspector,
            ownerName: this.state.ownerName,
            ownerAddress: this.state.ownerAddress,
            ownerZipCode: this.state.ownerZipCode,
            phone: this.state.phone,
            isCertified: this.state.isCertified,
            isPermited: this.state.isPermited,
            photoGallery: this.state.photoGallery,
            yearOfBorn: this.state.yearOfBorn,
            inspectDate: this.state.inspectDate,
            url: window.location.hostname + '/horse-permanent-travel-permit/edit/' 
        }
        const isValid = this.isFormValid(data);
        
        if (isValid) {
            var url = window.location.href
            var arr = url.split("/");
            var locationUrl = arr[0] + "//" + arr[2]
            var formData = new FormData();

            this.state.photoGallery.map((file, index) => {
                formData.append('animal_photo', file.image);
            });

            formData.append('breed', this.state.selectedBreed.value);
            formData.append('year_of_born', this.state.yearOfBorn);
            formData.append('color', this.state.selectedColor.value);
            formData.append('approx_ht', this.state.approxHeight);
            formData.append('name', this.state.name);
            formData.append('brand', this.state.selectedBrand.value);
            formData.append('sex', this.state.selectedSex.value);
            formData.append('registration_no', this.state.registration);
            formData.append('tattoo', this.state.tattoo);
            formData.append('cost_of_permit', this.state.costOfPermit);
            formData.append('live_stock_inspector', this.state.liveStockInspector);
            formData.append('date', moment(this.state.inspectDate[0]).format('YYYY-MM-DD'));
            formData.append('owner_name', this.state.ownerName);
            formData.append('owner_street_address', this.state.ownerAddress);
            formData.append('owner_lic_no', '');
            formData.append('city', this.state.ownerCity);
            formData.append('state', this.state.ownerState.label);
            formData.append('county', this.state.ownerCounty.label);
            formData.append('phone', this.state.phone);
            formData.append('zipcode', this.state.ownerCounty.label);
            formData.append('is_certified', this.state.isCertified);
            formData.append('is_permited', this.state.isPermited);
            
            if (this.state.drawImage.left !== '' && this.state.drawImage.left !== undefined){
                formData.append('mark_left', this.state.drawImage.left);
            }
            if (this.state.drawImage.right !== '' && this.state.drawImage.right !== undefined){
                formData.append('mark_right', this.state.drawImage.right);
            }
            if (this.state.drawImage.front !== '' && this.state.drawImage.front !== undefined) {
                formData.append('mark_center', this.state.drawImage.front);
            }
            
            formData.append('url', locationUrl + '/horse-permanent-travel-permit/edit/');
            
            
            if (!this.state.isEdit && this.state.horseId === 0){
                const url = getUrl("horse_permanent_travel_permit");
                postFormData(url, formData)
                    .then(res => {
                        if (res.data.code === 200 && res.data.status) {
                            this.props.postHorsePermanentTravelPermit(data);
                            alert(res.data.message);
                            this.setState({
                                isEdit: true,
                                horseId: res.data.data.id
                            })
                        }
                    })
                    .catch(err => console.log(err));
            }else{
                const url = getUrl("update_horse_permanent_travel_permit") +  this.state.horseId +'/';
                putFormData(url, formData)
                    .then(res => {
                        if (res.data.code === 200 && res.data.status) {
                            this.props.postHorsePermanentTravelPermit(data);
                            alert(res.data.message);
                            this.setState({
                                isEdit: true,
                                horseId: res.data.data.id
                            })
                        }
                    })
                    .catch(err => console.log(err));
                
            }
            
        }
    }

    
    handleAutoComplete = (data) => {
        const ownerState = this.state.stateOptions.find(option => option.value === data.state)
        let ownerCounty = this.state.countyOptions.find(option => option.value === data.county)
        // console.log(ownerState);
        if (ownerState.label !== "UTAH") {
            this.setState({
                countyOptions: this.state.defualtCountyOptions,
            })
            ownerCounty = this.state.defualtCountyOptions[0];
        }
        this.setState({
            ownerId: data.id,
            ownerName: data.name,
            ownerAddress: data.street_address,
            ownerCity: data.city,
            ownerState: ownerState,
            ownerCounty: ownerCounty,
            ownerZipCode: data.zip_code,
            activeSuggestionOwner: false,
            ownerNameErr: '',
            ownerAddressErr: ''
        })
    }

    handleChangeOwnerState = (SelectedOption) => {
        this.setState({ownerState:SelectedOption,ownerStateErr:''},() => {
            if (SelectedOption.label !== 'UTAH'){
                this.setState({
                    countyOptions: this.state.defualtCountyOptions
                })
            }else{
                this.setState({
                    countyOptions: this.state.countyOptionsOriginal
                })
            }
        })
    }
    handleChangeOwnerCounty = (SelectedOption) => {
        this.setState({ownerCounty:SelectedOption,ownerCountyErr:''})
    }


    drawImage = (drawImage) => {
        this.setState({ drawImage })
    }
    render() {
        return (
            <div className="row">
                <div className="col-lg-12">
                    <div className="panel-card-div panel-card-custom-div p-0" ref={el => (this.componentRef = el)}>
                        <div className="form-div form-border-div">
                            <div className="row mr-minus-10">
                                <div className="col-lg-3 col-md-4 pr-10">
                                    <DropDownList
                                        placeholder="Choose Breed"
                                        value={this.state.selectedBreed}
                                        handleChange={this.handleChangeBreed}
                                        options={this.state.breed} />
                                    {this.state.selectedBreedError !== "" ? <div style={{ fontSize: "14px", color: "red", margin: '-15px 0px 15px 0px' }}>{this.state.selectedBreedError}</div> : ''}
                                </div>

                                <div className="col-lg-3 col-md-4 pr-10">
                                    <InputField
                                        placeholder="Year Born"
                                        value={this.state.yearOfBorn}
                                        name="yearOfBorn"
                                        onChangeMethod={this.onInputChangeHandler}
                                    />
                                    {this.state.yearOfBornError !== "" ? <div style={{ fontSize: "14px", color: "red", margin: '20px 0px 15px 0px' }}>{this.state.yearOfBornError}</div> : ''}
                                </div>

                                <div className="col-lg-3 col-md-4 pr-10">
                                    <DropDownList
                                        placeholder="Choose Color"
                                        value={this.state.selectedColor}
                                        handleChange={this.handleChangeColor}
                                        options={this.state.color} />
                                    {this.state.selectedColorError !== "" ? <div style={{ fontSize: "14px", color: "red", margin: '-15px 0px 15px 0px' }}>{this.state.selectedColorError}</div> : ''}
                                </div>

                                <div className="col-lg-3 col-md-4 pr-10">
                                    <InputField
                                        placeholder="Approx. Ht."
                                        value={this.state.approxHeight}
                                        name="approxHeight"
                                        onChangeMethod={this.onInputChangeHandler}
                                    />
                                    {this.state.approxHeightError !== "" ? <div style={{ fontSize: "14px", color: "red", margin: '-15px 0px 15px 0px' }}>{this.state.approxHeightError}</div> : ''}
                                </div>

                                <div className="col-lg-3 col-md-4 pr-10">
                                    <InputField
                                        placeholder="Name"
                                        value={this.state.name}
                                        name="name"
                                        onChangeMethod={this.onInputChangeHandler}
                                    />
                                    {this.state.nameError !== "" ? <div style={{ fontSize: "14px", color: "red", margin: '-15px 0px 15px 0px' }}>{this.state.nameError}</div> : ''}
                                </div>

                                <div className="col-lg-3 col-md-4 pr-10">
                                    <DropDownList
                                        placeholder="Choose Brand"
                                        value={this.state.selectedBrand}
                                        handleChange={this.handleChangeBrand}
                                        options={this.state.brand} />
                                    {this.state.selectedBrandError !== "" ? <div style={{ fontSize: "14px", color: "red", margin: '-15px 0px 15px 0px' }}>{this.state.selectedBrandError}</div> : ''}
                                </div>

                                <div className="col-lg-3 col-md-4 pr-10">
                                    <DropDownList
                                        placeholder="Choose Sex"
                                        value={this.state.selectedSex}
                                        handleChange={this.handleChangeSex}
                                        options={this.state.sex} />
                                    {this.state.selectedSexError !== "" ? <div style={{ fontSize: "14px", color: "red", margin: '-15px 0px 15px 0px' }}>{this.state.selectedSexError}</div> : ''}
                                </div>

                                <div className="col-lg-3 col-md-4 pr-10">
                                    <InputField
                                        placeholder="Registration #"
                                        value={this.state.registration}
                                        name="registration"
                                        onChangeMethod={this.onInputChangeHandler}
                                    />
                                    {this.state.registrationError !== "" ? <div style={{ fontSize: "14px", color: "red", margin: '-15px 0px 15px 0px' }}>{this.state.registrationError}</div> : ''}
                                </div>

                                <div className="col-lg-3 col-md-4 pr-10">
                                    <InputField
                                      placeholder="Tattoo"
                                      value={this.state.tattoo}
                                      name="tattoo"
                                      onChangeMethod={this.onInputChangeHandler}
                                    />
                                    {/*<DropDownList*/}
                                    {/*    placeholder="Tatto Location"*/}
                                    {/*    value={this.state.selectedLocation}*/}
                                    {/*    handleChange={this.handleChangeLocation}*/}
                                    {/*    options={this.state.location} />*/}
                                    {/*{this.state.selectedLocationError !== "" ? <div style={{ fontSize: "14px", color: "red", margin: '-15px 0px 15px 0px' }}>{this.state.selectedLocationError}</div> : ''}*/}
                                </div>

                                <div className="col-lg-3 col-md-6 pr-10 order-md-1 order-1">
                                    <div className="form-group file-input-group active-group">
                                        <div className="custom-file custom-file-div">
                                            {/* <input type="file" className="custom-file-input" id="customFile" name="filename" placeholder="" onChange={this.uploadMultipleFiles} multiple /> */}
                                            <label onClick={this.handleOpenCameraPopUp} className="custom-file-label" htmlFor="customFile">Capture Animal Photo</label>

                                        </div>
                                    </div>
                                </div>

                                <div className="col-lg-3 col-md-6 pr-10 order-md-2 order-3">
                                    <div className="form-group form-btn-group active-group">
                                        <button onClick={this.handleModal} className="btn btn-draw btn-block" data-toggle="modal" data-target="#draw-mark-modal">Draw Mark <i className="pencil-icon"></i></button>
                                    </div>
                                </div>

                                <div className="col-lg-12 col-md-12 pr-10 order-md-3 order-2">
                                    <div className="form-group">
                                        <div className="upload-image-view">
                                            <div className="row mr-minus-5">
                                                {this.state.photoGallery.map((data, index) => {
                                                    return <div key={index} className="col-lg-3 col-md-3 col-6 col-20 plr-5">
                                                        <div className="img-thumb-box">
                                                            <img src={data.image} className="img-fluid img-responsive" alt="horse" />
                                                            <div className="btn btn-delete btn-delete-round" onClick={() => this.handleDeleteImage(data)}><i className="fe fe-x cancel-icon"></i></div>
                                                        </div>
                                                    </div>
                                                })}
                                            </div>
                                            {this.state.invalidImage !== "" ? <div style={{ fontSize: "14px", color: "red", margin: '0px 0px 15px 0px' }}>{this.state.invalidImage}</div> : ''}
                                        </div>
                                    </div>
                                </div>


                                <div className="col-lg-6 col-md-12 pr-10 order-md-4 order-4">
                                    <div className="custom-control custom-checkbox custom-checkbox-new custom-checkbox2 mt-20">
                                        <input type="checkbox" className="custom-control-input" name="isPermited" id="defaultUnchecked-permit" checked={this.state.isPermited} onChange={this.onChangeChecked} />
                                        <label className="custom-control-label" htmlFor="defaultUnchecked-permit">
                                            <span className="bold-underline">This permit void when animal changes owners,</span>
                                        This permit is issued in compliance with section 4-24-23 UCA 1985
                                        This permit must accompany the horse at all times when in transit in compliance
                                        with Utah laws governing inter-state movement, and proof of ownership.
                                        </label>
                                        {this.state.isPermitedError !== "" ? <div style={{ fontSize: "14px", color: "red", margin: '0px 0px 15px 0px' }}>{this.state.isPermitedError}</div> : ''}
                                    </div>
                                </div>

                            </div>
                        </div>

                        <div className="form-div form-border-div">
                            <div className="row mr-minus-10">

                                <div className="col-lg-3 col-md-4 pr-10">
                                    <InputField
                                        placeholder="Cost of permit"
                                        value={this.state.costOfPermit}
                                        name="costOfPermit"
                                        onChangeMethod={this.onInputChangeHandler}
                                    />
                                    {this.state.costOfPermitError !== "" ? <div style={{ fontSize: "14px", color: "red", margin: '-15px 0px 15px 0px' }}>{this.state.costOfPermitError}</div> : ''}
                                </div>

                                <div className="col-lg-3 col-md-4 pr-10">
                                    <InputField
                                        placeholder="Livestock inspector"
                                        value={this.state.liveStockInspector}
                                        name="liveStockInspector"
                                        onChangeMethod={this.onInputChangeHandler}
                                    />
                                    {this.state.liveStockInspectorError !== "" ? <div style={{ fontSize: "14px", color: "red", margin: '-15px 0px 15px 0px' }}>{this.state.liveStockInspectorError}</div> : ''}
                                </div>

                                <div className="col-lg-3 col-md-4 pr-10">
                                    <Flatpickr
                                        options={{
                                            enableTime: false,
                                            altInput: true,
                                            altFormat: "F j, Y",
                                            dateFormat: "Y-m-d",
                                        }}
                                        onChange={date => {
                                            this.setState({ inspectDate: date, 'inspectDateError': '' });
                                        }}
                                        placeholder="Select Date"
                                        value={this.state.inspectDate}
                                    />
                                    {this.state.inspectDateError !== "" ? <div style={{ fontSize: "14px", color: "red", margin: '20px 0px 15px 0px' }}>{this.state.inspectDateError}</div> : ''}
                                </div>

                                <div className="col-lg-3 col-md-4 pr-10">

                                </div>

                                <div className="col-lg-3 col-md-4 pr-10">
                                    <InputField
                                        placeholder="Owner"
                                        value={this.state.ownerName}
                                        name="ownerName"
                                        onChangeMethod={this.onInputChangeHandler}
                                    />
                                    {this.state.activeSuggestionOwner === true && this.state.ownerName.length >= 1 && this.state.filteredSuggestions.length > 0 &&
                                        <ul className="typeahead dropdown-menu" style={{ top: "46px", left: "10px", width: "221.328px", display: "block" }}>
                                            {
                                                this.state.filteredSuggestions.map((data, index) => {
                                                    return <li key={index} name="ownerName" onClick={() => this.handleAutoComplete(data)}>
                                                        <label>{data.name}</label>
                                                        <label className="address-link" href="#">{data.street_address}</label>
                                                    </li>
                                                })
                                            }
                                        </ul>   
                                    }
                                    {this.state.ownerNameError !== "" ? <div style={{ fontSize: "14px", color: "red", margin: '-15px 0px 15px 0px' }}>{this.state.ownerNameError}</div> : ''}
                                </div>

                                <div className="col-lg-3 col-md-4 pr-10">
                                    <InputField
                                        placeholder="Address"
                                        value={this.state.ownerAddress}
                                        name="ownerAddress"
                                        onChangeMethod={this.onInputChangeHandler}
                                    />
                                    {this.state.ownerAddressError !== "" ? <div style={{ fontSize: "14px", color: "red", margin: '-15px 0px 15px 0px' }}>{this.state.ownerAddressError}</div> : ''}
                                </div>

                                <div className="col-lg-3 col-md-4 pr-10">
                                    <InputField
                                        placeholder="City"
                                        value={this.state.ownerCity}
                                        name="ownerCity"
                                        onChangeMethod={this.onInputChangeHandler}
                                    />
                                    {this.state.ownerCityError !== "" ? <div style={{ fontSize: "14px", color: "red", margin: '20px 0px 15px 0px' }}>{this.state.ownerCityError}</div> : ''}
                                </div>
                                <div className="col-lg-3 col-md-4 pr-10">
                                    {/* <InputField
                                    placeholder="State"
                                    value={this.state.ownerState}
                                    name="ownerState"
                                    onChangeMethod={this.onInputChangeHandler} /> */}
                                    <DropDownList
                                        placeholder="Choose State"
                                        value={this.state.ownerState}
                                        handleChange={this.handleChangeOwnerState}
                                        options={this.state.stateOptions}
                                    />
                                    {this.state.ownerStateErr !== "" ? <div style={{ fontSize: "14px", color: "red", margin: '-15px 0px 15px 0px' }}>{this.state.ownerStateErr}</div> : null}
                                </div>
                                <div className="col-lg-3 col-md-4 pr-10">
                                    <DropDownList
                                        placeholder="Choose County"
                                        value={this.state.ownerCounty}
                                        handleChange={this.handleChangeOwnerCounty}
                                        options={this.state.countyOptions}
                                    />
                                    {/* <InputField
                                    placeholder="County"
                                    value={this.state.ownerCounty}
                                    name="ownerCounty"
                                    onChangeMethod={this.onInputChangeHandler} /> */}
                                    {this.state.ownerCountyErr !== "" ? <div style={{ fontSize: "14px", color: "red", margin: '-15px 0px 15px 0px' }}>{this.state.ownerCountyErr}</div> : null}
                                </div>

                                <div className="col-lg-3 col-md-4 pr-10">
                                    <InputField
                                        placeholder="Zip code"
                                        value={this.state.ownerZipCode}
                                        name="ownerZipCode"
                                        onChangeMethod={this.onInputChangeHandler}
                                    />
                                    {this.state.ownerZipCodeError !== "" ? <div style={{ fontSize: "14px", color: "red", margin: '-15px 0px 15px 0px' }}>{this.state.ownerZipCodeError}</div> : ''}
                                </div>

                                <div className="col-lg-3 col-md-4 pr-10">
                                    <InputField
                                        placeholder="Phone"
                                        value={this.state.phone}
                                        name="phone"
                                        onChangeMethod={this.onInputChangeHandler}
                                    />
                                    {this.state.phoneError !== "" ? <div style={{ fontSize: "14px", color: "red", margin: '-15px 0px 15px 0px' }}>{this.state.phoneError}</div> : ''}
                                </div>

                                <div className="col-lg-12 col-md-12 pr-10">
                                    <div className="custom-control custom-checkbox custom-checkbox-new mt-10">
                                        <input type="checkbox" className="custom-control-input" name="isCertified" id="defaultUnchecked-certify" checked={this.state.isCertified} onChange={this.onChangeChecked} />
                                        <label className="custom-control-label text-normal" htmlFor="defaultUnchecked-certify">
                                            I here by certify that I am the legal owner of the horse described here in
                                        </label>
                                        {this.state.isCertifiedError !== "" ? <div style={{ fontSize: "14px", color: "red", margin: '0px 0px 15px 0px' }}>{this.state.isCertifiedError}</div> : ''}
                                    </div>
                                </div>

                            </div>
                        </div>
                    </div>
                    <div className="tab-footer tab-footer2">
                        <div className="row ">
                            <div className="col-lg-12 justify-content-end ">

                                <div className="right-side-footer right-side-footer-final d-flex d-flex-sm align-items-end justify-content-between">
                                    <div className="width-100 width-right d-flex sm-width-100">

                                        <div className="step-btn width-50 sm-width-auto order-md-1 order-2">
                                            <Link style={this.state.horseId === 0 ? { pointerEvents: "none" } : null} target="_blank" to={`/horse-permanent-travel-permit-print/${this.state.horseId}`} className="btn btn-secondary2 btn-previous max-width-25"> <i className="fe fe-printer"></i> <span className="remove-mobile"> &nbsp; Print Permit</span></Link> 
                                        </div>

                                        <div className="step-btn width-25 sm-width-50 pr-10 order-md-2 order-1">
                                            <button href="#" className="btn btn-secondary btn-previous">Preview <span className="remove-mobile"> &nbsp; permit</span></button>
                                        </div>

                                        <div className="step-btn width-25 sm-width-50 order-md-3 order-3">
                                            <button href="#" onClick={this.handleSave} className="btn btn-primary btn-save">Save</button>
                                        </div>

                                    </div>
                                </div>

                            </div>
                        </div>
                    </div>
                </div>
                <DrawMarkModal display={this.state.renderMarkModal} drawImage={this.drawImage} drawedImage={this.state.drawImage} onHide={this.handleModal} />
                <CameraPopupModal display={this.state.cameraModal} onCaptureImage={this.handleCaptureImage} onHide={this.handleOpenCameraPopUp} />
            </div>
        )
    }
}
